import { useState } from "react";
import GameConfig from "./components/GameConfig";
import Gaming from "./components/Game";
import GameEnd from "./components/GameEnd";
// import Ads from "./components/Ads";
import { DEFAULT_CONFIG, STEP_TYPE } from "./config";
import styles from "./index.module.less";

const ChickenAChicken = () => {
  const [step, setStep] = useState(STEP_TYPE.CONFIG);
  const [config, setConfig] = useState(DEFAULT_CONFIG);
  const [win, setWin] = useState(false);

  const onChangeWin = (value: boolean) => {
    setWin(value);
    setStep(STEP_TYPE.END);
  };

  return (
    <div className={styles.box}>
      {/* 配置阶段 */}
      {step === STEP_TYPE.CONFIG && (
        <GameConfig
          config={config}
          setConfig={setConfig}
          startGame={() => {
            setStep(STEP_TYPE.GAMING);
          }}
        />
      )}
      {/* 游戏阶段 */}
      {step === STEP_TYPE.GAMING && <Gaming config={config} changeWin={onChangeWin} />}
      {/* 游戏结束阶段 */}
      {step === STEP_TYPE.END && <GameEnd win={win} changeStep={(type: number) => setStep(type)} />}
      {/* 广告 */}
      {/* <Ads /> */}
    </div>
  );
};

export default ChickenAChicken;
